import React, { useEffect } from 'react';
import { useSelector } from 'react-redux'
import { GAME_STATUS, selectGameStatus } from '../features/game/gameSlice'

let adsLoaded = false

export const DisplayAd_970_90 = () => {
  const gameStatus = useSelector(selectGameStatus)
  const collapse = gameStatus === GAME_STATUS.WAITING_START ? '' : 'collapse'

  useEffect(() => {
    if (!adsLoaded) {
      (window.adsbygoogle = window.adsbygoogle || []).push({})
      adsLoaded = true
    }
  })

  return (
    <div className={`ad ad-970-90 ${collapse}`}>
      {/* <!-- ffv-flipcard-d-ad-970-90 --> */}
      <ins className="adsbygoogle"
          style={{ display: 'inline-block', width: '970px', height: '90px' }}
          data-ad-client="ca-pub-2086960305398840"
          data-ad-slot="6843758370"></ins>
    </div>
  )
}

